import React, { Component, Fragment } from "react";
import Header from "./components_js/Header";
import Home from "./components_js/Home";
import ProjectMenu from "./components_js/ProjectMenu";
import ValveSelection from "./components_js/ValveSelection";
import "./App.css";



import { makeStyles } from '@material-ui/core/styles';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
//import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';



class App extends Component {
  constructor(props) {
    super(props);
    this.projectName = "xyz"
    this.state = {
      loggedIn: "Please log in"
    };
    this.addedValves = null
    this.setLoggedIn = this.setLoggedIn.bind(this)

  }

  setLoggedIn(value) {
    this.setState({ loggedIn: value })
  }
  render() {


    return (


      <div style={{ backgroundColor: "#D6DBDF" }} >
        <div style={{ backgroundColor: "#D6EAF8" }} className="header">
          <Header></Header>
          <AppBar position="static">
            <Toolbar>

              <Button onClick={() => this.setLoggedIn("Logged in")} color="inherit">{this.state.loggedIn}</Button>
            </Toolbar>
          </AppBar>
        </div>
        <div className="row">
          <div style={{ backgroundColor: '#FAFAFB' }} className="left">

            <ProjectMenu
            projectName = {this.projectName} 
            valves = {this.addedValves}/>
       
          </div>
          <div className="right">
            <ValveSelection />
          </div>

        </div>
      </div>



    );
  }


}

export default App;