import React, { useState } from 'react';
import { Button, ButtonGroup } from 'reactstrap';
import "./SecondaryToggle.css";

const SecondaryToggle = (props) => {

    const [rSelected, setRSelected] = useState(null);

    const myClick = (x) => (
        props.handler(props.options[x]),
        setRSelected(x)

        //console.log(rSelected, x, rSelected===1)
    )
    var arrayLength = props.options.length;

    let views = [];
    for (var step = 0; step < arrayLength; step++) {
        let y = step;
        // if (step == 12) {
        //     views.push(<div><p></p></div>)
        // }
        views.push(<Button key = {y} disabled={props.disabled} size="lg" color="primary" onClick={() => myClick(y)} active={rSelected == y}>{props.options[step]}</Button>
        )

    }

    //views=[]
    //console.log(rSelected)
    //console.log(views)
    if (props.doshow) {
        return (
            <div>
                <h5>{props.name}</h5>
                <div>
                    {views}
                </div >

            </div >
        );
    }


    return (

        <h5></h5>
    );

}

export default SecondaryToggle;